



.progrmas-cards-container{
    display: grid;
    grid-template-columns:repeat(auto-fill,minmax(300px,1fr));
    gap: 20px;
    padding: 0 var( --main-padding-left);
    align-items: center;
}



@media (max-width:768px) {
    .progrmas-cards-container{
        padding:0px 25px;
    }
    
}