/* make variables */
:root {
  --lightgray: #D9D9D9;
  --gray: #9c9c9c;
  --orange: #f48915;
  --darkGrey: #464D53;
  --caloryCard: #656565;
  --planCard: linear-gradient(210.41deg, #fa5042 1.14%, #ffa739 100.75%);
  --appColor:  #3c3f45;
  --main-transition: 0.3s;
  --main-margin-top: 100px;
  --main-margin-bottom: 100px;
  --main-padding-left:40px
}

.App {
  background-image: url(data:image/png;base64,);
  mix-blend-mode: overlay;
  background-color: var(--appColor);
  display: flex;
  flex-direction: column;
  /* overflow: hidden; */
  gap:4rem;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

/* ::-webkit-scrollbar{
  display: none;
} */

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}
body {
  font-family: 'Open Sans', sans-serif;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

.container {
  padding-left: 15px;
  padding-right:15px;
  margin-left: auto;
  margin-right: auto;

}

/* Small */
@media (min-width: 768px) {
  .container {
    width: 750px;
  }
}

/* Medium */
@media (min-width: 992px) {
  .container {
    width: 970px;
  }
}

/* Large */
@media (min-width: 1200px) {
  .container {
    width: 1170px;
  }
}


.stoke-text{
  color: transparent;
  font-family: Arial, Helvetica, sans-serif;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: white;
}

.blur{
  background-color: rgba(253,120,43);
  position: absolute;
  border-radius: 50%;
  filter:blur(165px);
  z-index: -1;
}

.heading-section{
  text-align: center;
  margin-bottom: 50px;
  font-size: 60px;
  font-style: italic;
  padding: 15px;
  line-height: 75px;
  color: white;
}

.heading-section span{
  padding-right: 50px;
  font-weight: 700;

}

@media (max-width:768px) {

  .heading-section{
      font-size:30px;
  }

  .heading-section span{
      padding-right:5px !important;
  }
  
}